*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.nav-list{
  height: 10vh;
  display: flex;
  width: 50%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style: none;
}

.nav-link{
  text-decoration: none;
  color: black;
  font-size: 18px;
}

.home{
  height: 90vh;
  position: absolute;
  width: 100%;
}

.home{
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.home-content{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}